import { HttpClient } from '@wix/sdk-types';
import {
  RecoverOptions,
  SendActivationEmailOptions,
  SendRecoveryEmailOptions,
  StateMachineResponse,
  recover as universalRecover,
  sendActivationEmail as universalSendActivationEmail,
  sendRecoveryEmail as universalSendRecoveryEmail,
} from './iam-recovery-v1-recovery-recovery.universal.js';

export const __metadata = { PACKAGE_NAME: '@wix/identity' };

export function sendRecoveryEmail(
  httpClient: HttpClient
): SendRecoveryEmailSignature {
  return (email: string, options?: SendRecoveryEmailOptions) =>
    universalSendRecoveryEmail(
      email,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface SendRecoveryEmailSignature {
  /**
   * Sends a member an email containing a customized link to a Wix-managed page
   * where the member can reset the password for their account.
   * @param - Email address associated with the account to recover.
   */
  (
    email: string,
    options?: SendRecoveryEmailOptions | undefined
  ): Promise<void>;
}

export function sendActivationEmail(
  httpClient: HttpClient
): SendActivationEmailSignature {
  return (identityId: string, options?: SendActivationEmailOptions) =>
    universalSendActivationEmail(
      identityId,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface SendActivationEmailSignature {
  /**
   * Sends an activation email with an activation token
   * making the transition from initial contact to a site member
   * @param - Id of the activating user
   */
  (
    identityId: string,
    options?: SendActivationEmailOptions | undefined
  ): Promise<void>;
}

export function recover(httpClient: HttpClient): RecoverSignature {
  return (recoveryToken: string, options?: RecoverOptions) =>
    universalRecover(
      recoveryToken,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface RecoverSignature {
  /** @param - recovery token */
  (
    recoveryToken: string,
    options?: RecoverOptions | undefined
  ): Promise<StateMachineResponse>;
}

